import React, { useContext, useEffect, useState } from "react";
import { EntryContext } from "./EntryProvider";
import { Entry } from "./Entry";
import { MoodContext } from "./mood/MoodProvider";
import * as axios from "axios";
export const EntryList = () => {
  const { entries, searchEntries } = useContext(EntryContext);
  const { moods, getMoods } = useContext(MoodContext);
  const [filteredEntries, setEntries] = useState([]);
  const [searchTerm, setTerm] = useState("");
  const [count, setCount] = useState(1);
  const [moodSelected, setMoodSelected] = useState("");
  const [page, setPage] = useState(1);
  const [pagesArr, setPagesArr] = useState([]);
  const [loading, setLoading] = useState(false);

  const searchEntires = () => {
    console.log("searchEntires -> term", searchTerm);
    setLoading(true);
    axios
      .get("http://167.172.132.44:1337/shredden-pieces/search", {
        params: {
          _limit: 30,
          _start: (page - 1) * 30,
          _q: searchTerm,
          _sort: "diaryDate:DESC",
        },
      })
      .then((res) => {
        setLoading(false);
        setEntries(res.data.pieces);
        console.log("searchEntires -> res.data.pieces", res.data.pieces);
        setCount(res.data.count);
        let pages = [];
        let i = 1;
        let pagesCount = res.data.count / 30;
        while (i <= pagesCount) {
          pages.push(i);
          i++;
        }
        setPagesArr(pages);
        console.log("EntryList -> res", entries);
      });
  };

  const handlePageClick = (page) => {
    setPage(page);
    console.log("handlePageClick -> page", page);
  };

  const handleNextPage = () => {
    if (page < pagesArr.length) setPage(page + 1);
  };
  const handlePreviousPage = () => {
    if (page > 1) setPage(page - 1);
  };
  useEffect(() => {
    searchEntires("");
  }, [page, searchTerm]);

  return (
    <>
      <div class="container px-4 px-lg-5">
        <div class="row pt-4 gx-4 gx-lg-5 justify-content-center">
          <h1 className="py-4">Total Pieces ({count})</h1>
          {/* Search Row */}
          <div className="search-row col-lg-6 col-xs-12 col-sm-12 form-group d-flex">
            <input
              onChange={(e) => setTerm(e.target.value)}
              className="form-control"
            />
            <button
              onClick={() => searchEntires(searchTerm)}
              className="btn text-white"
            >
              Search
            </button>
          </div>
          {/* Pagination Array */}
          <div className="col-12">
            <nav
              className="overflow-scroll"
              aria-label="Page navigation example"
            >
              <ul className="pagination">
                <li className="page-item">
                  <a
                    onClick={() => handlePreviousPage()}
                    className="btn btn-link text-white"
                    aria-label="Previous"
                  >
                    <span aria-hidden="true">&laquo;</span>
                    <span class="sr-only">Previous</span>
                  </a>
                </li>
                {pagesArr.map((x, idx) => (
                  <li key={x} class="page-item">
                    <a
                      className={
                        "btn btn-link text-white " +
                        (x == page ? " text-bold" : "")
                      }
                      onClick={() => handlePageClick(x)}
                    >
                      {x}
                    </a>
                  </li>
                ))}

                <li class="page-item">
                  <a
                    onClick={() => handleNextPage()}
                    className="btn btn-link text-white"
                    aria-label="Next"
                  >
                    <span class="sr-only">Next</span>
                    <span aria-hidden="true">&raquo;</span>
                  </a>
                </li>
              </ul>
            </nav>
          </div>

          {loading && (
            <div className="d-flex justify-content-center container text-center margin-auto">
              <div class="loader"></div>
            </div>
          )}
          <div
            className={
              "col-md-10 col-lg-8 col-xl-7" + (loading ? "d-none" : "")
            }
          >
            {/* Post Preview */}

            <div className="entries">
              {filteredEntries.map((entry) => {
                return <Entry key={entry.id} entry={entry} moods={moods} />;
              })}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
